body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding: 0.5rem;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.flex-row {
  display: flex;
  flex-direction: row;
}

nav li {
  margin-right: 1.5rem;
}

.menu {
  padding: 1rem;
  display: inline-block;
}
.item {
  padding: 5px;
  width: 12rem;
  height: 9rem;
  margin: 0.5rem;
  font-weight: bold;
  /* display: inline-block; */
  float: left;
  border: 1px solid #b3b3b3;
}

.cant {
  font-weight: 100;
}
.order {
  width: 50%;
  padding: 1rem;
  display: inline-block;
}
.order-box {
  padding: 5px;
  width: 85%;

  margin: 0.5rem;
  border: 1px solid #b3b3b3;
}
.order-box p {
  border-bottom: 1px solid #b3b3b3;
}
.total p {
  border: none;
  font-weight: bold;
}

